.ec-orders-list{max-width:700px;margin:0 auto}@media (min-width:768px){.ec-orders-list__item{display:-webkit-box;display:flex;-webkit-box-pack:justify;justify-content:space-between}.ec-orders-list__item>div{-webkit-box-flex:1.4;flex:1.4 1 0}.ec-orders-list__item>span{-webkit-box-flex:1;flex:1 1 0;text-align:right}}.ec-orders-list__item:hover .ec-orders-list__number{text-decoration:underline}.ec-orders-list__number{padding:0 1rem;border-left:.5rem solid var(--primary-lightest);color:var(--primary)}.ec-orders-list__number--open{border-left-color:var(--info)}.ec-orders-list__number--closed{border-left-color:var(--success)}.ec-orders-list__number--cancelled{border-left-color:var(--danger)}.ec-orders-list__status{font-weight:700}@media (max-width:767.98px){.ec-orders-list__status{float:right}}.ec-orders-list__status--open{color:var(--info)}.ec-orders-list__status--closed{color:var(--success)}.ec-orders-list__status--cancelled{color:var(--danger)}